*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

   /* navbar */

   .navbar{
    font-size: 17px;
    line-height: 26px;
    text-transform: uppercase;
    box-shadow: 0px 8px 6px -6px gray;
    --webkit-box-shadow:0px 8px 6px -6px gray; 
    border-bottom: 0.5px gray; 
  
  }
  .nav-link{
    font-weight: 300 !important;
  }

  .active{
    border-bottom: 1px solid black;
  }

  .navbar-brand{
    font-weight: 700;
    font-family: 'roboto',sans-serif;
    letter-spacing: 3px;
  }

  /* footer */

  .footer{
    color: white;
    padding: 25px;
    background-color: black;
  }
  .footer a{
    text-decoration: none;
    color: white;
    padding: 10px;
  }
  .footer a:hover{
    color: gray;
    border-bottom: 1px solid white;
  }

  /* page not found */
  .pnf{
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .pnf-title{
    font-size: 100px;
    font-weight: 700;
  }
  .pnf-btn{
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
  }
  .pnf-btn:hover{
    background-color: black;
    color: white;
  }

  /* contact us */

  .contactus{
    padding: 0px;
    margin: 0px;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center ;
  }



  /* register */

  .form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    flex-direction: column;
    background-color: #ffdee9;
    background-image: linear-gradient(0deg,#ffdee9 0% , #b5fffc 100%);
  }
  .form-container form{
    box-shadow: rgb(0,0,0,0.35) 0px 5px 15px;
    padding: 20px;
    background-color: #fff;
  }
  .form-container form .title{
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
  }
  .form-container form input{
    border: none;
    border-bottom: 1px solid #000;
    border-radius: 0;
  }
  .form-container form button {
    border:1px solid black;
    border-radius: 0;
    background-color: #000;
    color: white;
    width: 250px;
  }
  .forgot{
    background-color: white;
    color: black;
    font-weight: bold;
  }

  .product-link{
    text-decoration: none;
  }
  .card-img-top{
    height: 250px;
  
  }
  .card{
    height: 500px;
  }
 